export default function Icons(n, color){
    let iconsList = [
        wifi(color), 
        pool(color),
        kitchen(color),
        tv(color),
        ac(color),
        pet(color),
        parking(color),
        creditCard(color),
        smoke(color),
        party(color),
        checkIn(color),
        noSmoke(color)
    ]
    return iconsList[n];
}
